import React, { useState } from 'react';
import 'react-modern-calendar-datepicker/lib/DatePicker.css';
import DatePicker from 'react-modern-calendar-datepicker';
import { Grid, Box } from '@material-ui/core';
import './convert.css';
import { Helmet } from 'react-helmet';
import moment from 'jalali-moment';
import Layout from '../../layouts';

const Convet = () => {
    const [firstDay, setFirstDay] = useState(null);
  const [secoundDay, setsecoundDay] = useState(null);
  if (firstDay) {
    const mo = moment(`${firstDay.year}/${firstDay.month}/${firstDay.day}`, 'YYYY/M/D').locale('fa');
    const persianDate = {
      year: mo.format('YYYY'),
      month: mo.format('MM'),
      day: mo.format('DD'),
    };
    console.log(persianDate);
  }

    return (
        <Layout>
            <Helmet titleTemplate="%s - ایونتا">
                <title>تبدیل تاریخ</title>
            </Helmet>
           <Box mt={4}>
          <Grid
            container
            direction="row"
            justify="center"
            alignItems="center"

          >
            <DatePicker
              value={firstDay}
              onChange={setFirstDay}
              inputPlaceholder="میلادی"
              calendarClassName="responsive-calendar"
              shouldHighlightWeekends
            />
            <p style={{ margin: '0 2em' }}>به</p>
            <DatePicker
              value={secoundDay}
              onChange={setsecoundDay}
              inputPlaceholder="شمسی"

              locale="fa"
              calendarClassName="responsive-calendar"
              shouldHighlightWeekends
            />
          </Grid>
           </Box>

        </Layout>
    );
};

export default Convet;
